header{
   height: 100vh;
   padding-top:  4rem;
   overflow: hidden; 
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* cta  */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
}
.header__socials{
display: flex;
flex-direction: column;
align-items: center;
position: absolute;
left: 0;
bottom: 3rem;
}
.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

.me{

    background: linear-gradient(rgb(3, 67, 99), transparent); 
    width: 22rem;
    height: 20rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius:12rem ;
    overflow: hidden;
    padding: 0 1rem 0 0;
}
.muaz{
    height: 70rem;
    width: 22rem;
}
.scroll__down{
    position: absolute;
    right:  -2.3rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}
@media screen and (max-width: 1024px) {
   header{
    
    height:  100vh;
   } 
}

@media screen and (max-width: 600px) {
    header{
        height:  100vh;
        margin-top: 0rem;
    }
    .header__socials,
    .scroll__down{
    display: none;
    }
}